import { Injectable } from '@angular/core';
import { Observable, of, throwError } from 'rxjs';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { catchError, tap, map } from 'rxjs/operators';
import { Cases } from './cases';
import { Statistic } from './statistic';


const httpOptions = {
  headers: new HttpHeaders({'Content-Type': 'application/json'})
};

const apiUrl = 'http://localhost:3000/Cases';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private http: HttpClient) { }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  getCases() {
    return this.http.get(apiUrl);
  }

  getCasesById(id: number){
    return this.http.get(apiUrl+"/"+id);
  }

  addCases(cases: Cases)
  {
    return this.http.post<Cases>(apiUrl, cases);
  }

  updateCases(id: number, cases: Cases){
    return this.http.put(apiUrl+"/"+id, cases);
  }

  deleteCases(id: number)
  {
    return this.http.delete<Cases>(apiUrl+"/"+id);
  }

  getStatistic(status: string): Observable<Statistic> {
    const url = `${apiUrl}/daily/${status}`;
    return this.http.get<Statistic>(url);
  }
}
