import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiService } from '../api.service';
import { Cases } from '../cases';

@Component({
  selector: 'app-cases-details',
  templateUrl: './cases-details.component.html',
  styleUrls: ['./cases-details.component.scss']
})
export class CasesDetailsComponent implements OnInit {

  constructor(private route: ActivatedRoute, private api: ApiService, private router: Router) { }

  ngOnInit(): void {
    
  }

  cases: Cases = { 
    _id: null, 
    name: '', 
    gender: '', 
    age: null, 
    address: '', 
    city: '', 
    country: '', 
    status: '', 
    updated: null };
  

  getCasesDetails(id: number) {
    this.api.getCasesById(id).subscribe((data: any) => {this.cases = data;});
  }

  deleteCases(id: number) {this.api.deleteCases(id).subscribe();
  }

}
